import { Component, OnInit, Inject, ElementRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-bussiness-popup',
  templateUrl: './bussiness-popup.component.html',
  styleUrls: ['./bussiness-popup.component.scss']
})
export class BussinessPopupComponent implements OnInit {
  htmlContent: string | any;

  constructor(
    public dialogRef: MatDialogRef<BussinessPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private sanitizer: DomSanitizer
  ) {}

  ngOnInit() {
    if (this.data && this.data.image) {
      this.htmlContent = this.sanitizer.bypassSecurityTrustResourceUrl(
        `assets/images/${this.data.image}.png`
      );
    } else {
      this.dialogRef.close();
    }
  }
}
