import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { AnimationService } from 'src/app/services/animation.service';
import { TweenMax, Linear } from 'gsap/all';

declare var ScrollMagic: any;
declare var jQuery: any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  providers: [AnimationService]
})
export class HomeComponent implements OnInit {
  @ViewChild('topSlide')
  topSlide: ElementRef<HTMLElement>;

  constructor(private animations: AnimationService) {}

  ngOnInit() {
    this.setPinTopSlide();
    this.boxesAnimation();
    this.autoplay();
  }

  scrollToTop() {
    this.animations.scrollTo('.top-slide', 0);
  }

  setPinTopSlide() {
    this.animations.runTween(
      '.menu-trigger',
      200,
      TweenMax.fromTo(
        '.pinned-menu',
        100,
        {
          height: '0px'
        },
        {
          height: '100px'
        }
      )
    );
    const controller = new ScrollMagic.Controller();
    const scene = new ScrollMagic.Scene({
      triggerElement: '.top-slide',
      duration: 700
    });
    scene.triggerHook(0);
    scene.setTween('.slide-pinned', {
      zIndex: 0,
      y: '100%',
      ease: Linear.easeNone
    });
    // scene.addIndicators({ name: 'Start animation ' });
    scene.addTo(controller);
  }

  boxesAnimation() {
    this.animations.runTween(
      '.clasiffied-information',
      100,
      TweenMax.fromTo(
        '.info1',
        100,
        {
          y: '120',
          opacity: '0'
        },
        {
          y: '60',
          opacity: '1'
        }
      )
    );
    this.animations.runTween(
      '.clasiffied-information',
      220,
      TweenMax.fromTo(
        '.info2',
        100,
        {
          y: '180',
          opacity: '0'
        },
        {
          y: '60',
          opacity: '1'
        }
      )
    );
    this.animations.runTween(
      '.clasiffied-information',
      250,
      TweenMax.fromTo(
        '.info3',
        100,
        {
          y: '240',
          opacity: '0'
        },
        {
          y: '60',
          opacity: '1'
        }
      )
    );
  }

  autoplay() {
    this.animations.runTween(
      '#know-information',
      80,
      TweenMax.from('#know-information', 0.1, {
        opacity: '1',
        onComplete: () => {
          try {
            jQuery('#dashboard-video').get()[0].play();
          } catch (errVideo) { }
        }
      })
    );
  }
}
