import { Injectable } from '@angular/core';

declare var jQuery: any;
declare var ScrollMagic: any;

@Injectable({
  providedIn: 'root'
})
export class AnimationService {
  constructor() {}

  runTween(triggerElement: string, duration: number, tween: any, hook?: number) {
    const controller = new ScrollMagic.Controller();
    const scene = new ScrollMagic.Scene({
      triggerElement,
      duration
    }).setTween(tween);
    // scene.addIndicators({ name: 'Start animation ' + triggerElement });
    if (hook) {
      scene.triggerHook(hook);
    }
    scene.addTo(controller);
    return scene;
  }

  scrollTo(selector: string, offset: number = 0, speed: string | number = 'slow') {
    jQuery('html, body').animate({
      scrollTop: (jQuery(selector).offset().top + (offset))
    }, speed);
  }
}
