import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-industrial-grid',
  templateUrl: './industrial-grid.component.html',
  styleUrls: ['./industrial-grid.component.scss']
})
export class IndustrialGridComponent implements OnInit {

  images: any[] = [
    'assets/images/warehouse.jpg',
    'assets/images/warehouse2.jpg',
    'assets/images/print-warehouse.jpg',
    'assets/images/tablet-bussiness.jpg'
  ];

  constructor(private sanitizer: DomSanitizer) {
    this.images = this.images
      .map(image => {
        return this.sanitizer.bypassSecurityTrustResourceUrl(image);
      });
  }

  ngOnInit() {
  }

}
