import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-header-contact-information',
  templateUrl: './header-contact-information.component.html',
  styleUrls: ['./header-contact-information.component.scss']
})
export class HeaderContactInformationComponent implements OnInit {

  lang: any;

  constructor() { }

  ngOnInit() {
  }

  setLanguage(language: string) {
    localStorage.setItem('kryptos.language', language);
    window.location.reload();
  }

}
