import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { BussinessPopupComponent } from './bussiness-popup/bussiness-popup.component';
import { DomSanitizer } from '@angular/platform-browser';
import { TweenMax } from 'gsap/all';

declare var jQuery: any;

@Component({
  selector: 'app-bussiness-boxes-info',
  templateUrl: './bussiness-boxes-info.component.html',
  styleUrls: ['./bussiness-boxes-info.component.scss']
})
export class BussinessBoxesInfoComponent implements OnInit {
  activeInformation = false;
  imagePreview = '';
  htmlContent: string | any;
  constructor(private dialog: MatDialog, private sanitizer: DomSanitizer) {}

  ngOnInit() {}

  showPopup(image: string) {
    const dialog = this.dialog.open(BussinessPopupComponent, {
      data: {
        image
      }
    });
    dialog.afterClosed().subscribe(data => {
      console.log(data);
    });
  }

  showInformation(image: string) {
    this.activeInformation = true;
    this.imagePreview = image;
    this.htmlContent = this.sanitizer.bypassSecurityTrustResourceUrl(
      `assets/images/${image}.png`
    );
    this.animateInformation(image);
  }

  closeInformation() {
    jQuery('.clasiffied-information').css({
      'padding-bottom': '0px'
    });
    jQuery('body').removeClass('stop-scrolling');
    jQuery('body').unbind('touchmove');
    TweenMax.fromTo(
      '.information-preview',
      0.2,
      {
        opacity: '1',
        width: '100%',
        height: 'calc(100% + 50px)'
      },
      {
        opacity: '0',
        width: '0px',
        height: '0px'
      }
    );
  }
  animateInformation(image: string) {
    let normalView = true;
    let toAnimation = {
      opacity: '1',
      width: '100%',
      height: 'calc(100% + 50px)',
      left: '0%',
      top: '20px',
      position: 'absolute',
      zIndex: '100'
    };
    jQuery('.clasiffied-information').css({
      'padding-bottom': '300px'
    });
    if (window.innerWidth <= 480) {
      jQuery('.clasiffied-information').css({
        'padding-bottom': '500px'
      });
      jQuery('body').addClass('stop-scrolling');
      jQuery('body').bind('touchmove', (e) => {
        e.preventDefault();
      });
      normalView = false;
      toAnimation = {
        opacity: '1',
        width: '100%',
        height: 'auto',
        left: '0%',
        top: '100px',
        position: 'fixed',
        zIndex: '120'
      };
    }
    if (image === 'data-prediction') {
      TweenMax.fromTo(
        '.information-preview',
        0.3,
        {
          opacity: '0',
          width: '0px',
          height: '0px'
        },
        toAnimation
      );
    } else if (image === 'automatic-clasification') {
      TweenMax.fromTo(
        '.information-preview',
        0.3,
        {
          opacity: '0',
          width: '0px',
          height: '0px',
          left: '50%'
        },
        toAnimation
      );
    } else if (image === 'efficient-budget') {
      TweenMax.fromTo(
        '.information-preview',
        0.2,
        {
          opacity: '0',
          width: '0px',
          height: '0px',
          left: '100%',
          top: '100%'
        },
        toAnimation
      );
    }
  }
}
