import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { LangPipe } from 'src/app/pipes/lang.pipe';
import { TweenMax } from 'gsap/all';

@Component({
  selector: 'app-unique-features',
  templateUrl: './unique-features.component.html',
  styleUrls: ['./unique-features.component.scss'],
  providers: [LangPipe]
})
export class UniqueFeaturesComponent implements OnInit {
  @ViewChild('slideImage')
  slideImage: ElementRef<HTMLElement>;

  position = 0;
  items = [];
  currentSlide = {
    title: '',
    subtitle: '',
    image: '',
    icon: '',
    description: '',
    position: 0
  };

  constructor(private lang: LangPipe) {
    this.position = 0;
  }

  ngOnInit() {
    this.items = [
      {
        title: this.lang.transform('unique_feature'),
        subtitle: this.lang.transform('company_confidential'),
        image: 'assets/images/carrousel_1.jpg',
        icon: '',
        description: ''
      },
      {
        title: this.lang.transform('unique_feature'),
        subtitle: this.lang.transform('improve_tools'),
        image: 'assets/images/carrousel_2.jpg',
        icon: 'assets/icons/seguridad.svg',
        description: this.lang.transform('dpls')
      },
      {
        title: this.lang.transform('unique_feature'),
        subtitle: this.lang.transform('cyber_culture'),
        image: 'assets/images/4a.jpg',
        icon: 'assets/icons/hombre.svg',
        description: this.lang.transform('critical_documents')
      },
      {
        title: this.lang.transform('unique_feature'),
        subtitle: this.lang.transform('optimize_policies'),
        image: 'assets/images/4a.jpg',
        icon: 'assets/icons/cohete.svg',
        description: this.lang.transform('measures_critical')
      }
    ];
    this.items.forEach(image => {
      const img = new Image(1, 1);
      img.src = image.image;
      img.style.display = 'none';
      document.body.appendChild(img);
    });
    this.selectSlide(0);
    setInterval(() => {
      this.move(1);
    }, 12000);
  }

  move(direction: number) {
    this.slideImage.nativeElement.style.opacity = '0';
    if (direction === 0) {
      this.position--;
      if (this.position < 0) {
        this.position = this.position = this.items.length - 1;
      }
    } else {
      this.position++;
      if (this.position >= this.items.length) {
        this.position = 0;
      }
    }
    this.animateSlide();
  }
  animateSlide() {
    this.currentSlide = {
      ...this.currentSlide,
      ...this.items[this.position]
    };
    TweenMax.fromTo(
      '#textToSlide',
      0.4,
      {
        x: '-200',
        opacity: '0'
      },
      {
        x: '0',
        opacity: '1'
      }
    );
    setTimeout(() => {
      this.currentSlide = {
        ...this.currentSlide,
        ...this.items[this.position],
        position: this.position
      };
      this.slideImage.nativeElement.style.opacity = '1';
    }, 400);
  }
  selectSlide(position: number) {
    this.position = position;
    this.animateSlide();
  }
}
